  /* This is VIP */

  .flex-container {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-wrap: wrap;
        flex-wrap: wrap;
    -ms-flex-align: center;
        align-items: center;
    -ms-flex-pack: center;
        justify-content: center;
    /* background-color: rgb(243, 240, 240); */
  }

  /* This VIP */

  .item {
    position: relative;
    display: -ms-flexbox;
    display: flex;
    height: 300px;
    width: 400px;
    margin: 20px;
    -ms-flex-direction: column;
        flex-direction: column;
    -ms-flex-wrap: wrap;
        flex-wrap: wrap;
  }

  /* VIP for not warping image size */

  .thumb {
    display: block;
    width: 100%;
    height: auto;
    -webkit-transition: .5s ease;
    -o-transition: .5s ease;
    transition: .5s ease;
  }

  /* This is VIP - holds the overlay controls */

  .blog-overlay {
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    height: 100%;
    width: 100%;
    opacity: 0;
    -webkit-transition: .3s ease;
    -o-transition: .3s ease;
    transition: .3s ease;
    background-color: #fbc800;
    /*border-radius: 5px; */
  }

  /*  Need this or overlay goes away altogether */

  .item:hover .blog-overlay {
    height: 100%;
    opacity: 1;
  }

  /* This changed the placement of all the text on overlay - VIP */

  .text {
    white-space: nowrap;
    color: white;
    color: #40484b;
    font-size: 20px;
    text-align: center;
    position: absolute;
    overflow: hidden;
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
    -ms-transform: translate(-50%, -50%);
    /* background-color: pink; */
  }

  .blogh3, h3 {

    font-size: 30px;
    font-family: "HCo Verlag", "CenturyGothic", AppleGothic, Gill Sans, Gill Sans MT, sans-serif;
    font-weight: 150;
    letter-spacing: -.5pt;
  }

  .blogp {
    font-family: "HCo Verlag", "CenturyGothic", AppleGothic, Gill Sans, Gill Sans MT, sans-serif;
    font-weight: 150;
    letter-spacing: -.5pt;
  }
